// SCSS Variables
$duration: 200ms;
$font: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, "Helvetica Neue", Arial, "Noto Sans", sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol", "Noto Color Emoji";
$font-monospace: SFMono-Regular, Menlo, Monaco, Consolas, "Liberation Mono", "Courier New", monospace;
// Dimension variables
$size-xs: 12px;
$size-sm: 14px;
$size-md: 16px;
$size-lg: 18px;
$size-xl: 20px;
$radius: 4px;
$nav-h: 50px;
$header-h: 44px;
$subheader-h: 30px;
$footer-h: 50px;
$dx: 16px;
$dy: 12px;
$sx: 12px;
$sy: 16px;
$dims-xs: 2px;
$dims-sm: 4px;
$dims-md: 8px;
$dims-lg: 16px;
$dims-xl: 32px;
:root {
  --size-xs: #{$size-xs};
  --size-sm: #{$size-sm};
  --size-md: #{$size-md};
  --size-lg: #{$size-lg};
  --size-xl: #{$size-xl};
  --radius: #{$radius};
  --nav-h: #{$nav-h};
  --header-h: #{$header-h};
  --subheader-h: #{$subheader-h};
  --footer-h: #{$footer-h};
  --dx: #{$dx};
  --dy: #{$dy};
  --sx: #{$sx};
  --sy: #{$sy};
}
// Color variables
:root {
  --primary: #222;
  --secondary: #C1A063;
  --link: #4392F1;
  --success: #23d160;
  --warning: #ffde56;
  --danger: #BB0909;
  --major: #333;
  --info: #666;
  --hint: #999;
  --invert: white;
  --na: #d5d5d5;
  --sep: #e5e5e5;
  --overlay: rgba(0, 0, 0, .5);
  --shadow: rgba(0, 0, 0, .06);
  --main-bg: white;
  --body-bg: #F1F5F7;
  --nav-bg: rgba(255, 255, 255, .8);
  --nav-menu-bg: var(--main-bg);
  --header-bg: rgba(255, 255, 255, .7);
}

// Keyframes
@keyframes spin-around {
  from {
    transform: rotate(0);
  }
  to {
    transform: rotate(359deg);
  }
}

// Mixins
$media-sm: 576px;
$media-md: 768px;
$media-lg: 1024px;
$media-xl: 1200px;
$media-sm-max-width: 540px;
$media-md-max-width: 720px;
$media-lg-max-width: 960px;
$media-xl-max-width: 1140px;
// @mixin media-xs { @content; } // The default media.
@mixin media-sm {
  @media (min-width: $media-sm) { @content; }
}
@mixin media-md {
  @media (min-width: $media-md) { @content; }
}
@mixin media-lg {
  @media (min-width: $media-lg) { @content; }
}
@mixin media-xl {
  @media (min-width: $media-xl) { @content; }
}
@mixin media-mobile {
  @media (max-width: $media-lg - 1) { @content; }
}
@mixin media-desktop {
  @media (min-width: $media-lg) { @content; }
}
@mixin transition-ease($property: all) {
  transition: $property $duration ease;
}
@mixin transition-ease-out($property: all) {
  transition: $property $duration ease-out;
}
@mixin clear-transition {
  transition: none;
}
@mixin ellipsis($max-width: inherit) {
  max-width: $max-width;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}
@mixin line-clamp($lines: 3, $line-height: 1.6) {
  display: -webkit-box;
  -webkit-line-clamp: $lines;
  -webkit-box-orient: vertical;
  overflow: hidden;
  line-height: $line-height;
  max-height: 1em * $line-height * $lines;
}
@mixin shadow {
  box-shadow: 0 2px 11px var(--shadow);
}
@mixin backdrop($bg) {
  background-color: $bg;
  backdrop-filter: saturate(180%) blur(20px);
}
@mixin clear-backdrop {
  background-color: initial;
  backdrop-filter: initial;
}
@mixin sticky($top: 0, $z-index: 1) {
  position: sticky;
  top: $top;
  z-index: $z-index;
}

// Default
* {
  margin: 0;
  padding: 0;
  position: relative;
  line-height: inherit;
  color: var(--fg);
  word-break: inherit;
}
*, :before, :after {
  box-sizing: border-box;
}
html {
  --fg: var(--major);
  font-size: $size-md;
  font-weight: normal;
  font-family: $font;
  line-height: 1.2;
  background-color: var(--body-bg);
  touch-action: manipulation;
  -webkit-tap-highlight-color: transparent;
  -webkit-overflow-scrolling: touch;
  -webkit-font-smoothing: antialiased;
  &.dark {
    --primary: #222;
    --secondary: #C1A063;
    --link: #4392F1;
    --success: #23d160;
    --warning: #ffde56;
    --danger: #BB0909;
    --major: #fff;
    --info: #eee;
    --hint: #ddd;
    --invert: black;
    --na: #252525;
    --sep: #353535;
    --overlay: rgba(255, 255, 255, .5);
    --shadow: rgba(255, 255, 255, .06);
    --main-bg: #343a40;
    --body-bg: #333;
    --nav-bg: rgba(0, 0, 0, .2);
    --nav-menu-bg: var(--main-bg);
    --header-bg: rgba(0, 0, 0, .3);
  }
}
a {
  --fg: var(--link);
  text-decoration: none;
  // @include transition-ease;
  &:hover {
    --fg: inherit;
  }
}
p {
  line-height: 1.6;
}
svg {
  fill: currentColor;
}
h1, h2, h3, h4, h5, h6 {
  font-weight: bolder;
  margin: 1em 0;
}
h1 {
  font-size: 2.5rem;
}
h2 {
  font-size: 2rem;
}
h3 {
  font-size: 1.75rem;
}
h4 {
  font-size: 1.5rem;
}
h5 {
  font-size: 1.25rem;
}
h6 {
  font-size: 1rem;
}
ol, ul {
  padding-left: 1.5em;
}
a, label, button, [type="submit"], [type="reset"], [type="button"], [data-cursor="pointer"] {
  cursor: pointer;
}
button, input, optgroup, select, textarea {
  line-height: inherit;
}
textarea {
  resize: vertical;
}
b, strong {
  font-weight: bolder;
}
code, kbd, samp {
  font-family: $font-monospace;
}
code {
  white-space: pre;
  --fg: var(--primary);
}
::placeholder {
  color: var(--hint);
}
[readonly], [disabled] {
  cursor: not-allowed;
}
[tabindex="-1"] {
  outline: none;
}

// Loading
[data-loading="true"], [data-loading="fullscreen"], [data-loading="before"], [data-loading="after"] {
  pointer-events: none;
}
[data-loading="true"]:after, [data-loading="fullscreen"]:after, [data-loading="before"]:before, [data-loading="after"]:after {
  --width: 1.2em;
  content: '';
  width: var(--width);
  height: var(--width);
  border-radius: 50%;
  border: 2px solid;
  border-color: transparent transparent var(--fg) var(--fg);
  animation: spin-around .5s infinite linear;
}
[data-loading="true"]:after, [data-loading="fullscreen"]:after {
  display: block;
  z-index: 3;
  top: calc(50% - var(--width) / 2);
  left: calc(50% - var(--width) / 2);
}
[data-loading="true"] {
  color: transparent;
  &:after {
    position: absolute;
  }
}
[data-loading="fullscreen"]:after {
  --fg: var(--primary);
  position: fixed;
  font-size: 300%;
}
[data-loading="before"]:before, [data-loading="after"]:after {
  display: inline-block;
  vertical-align: text-bottom;
}
[data-loading="before"]:before {
  margin-right: .5em;
}
[data-loading="after"]:after {
  margin-left: .5em;
}

// Layout
@mixin row-col($prefix, $x) {
  $ratio: $x * 100 / 12;
  > .#{$prefix}-#{$x} {
    flex: 0 0 #{"#{$ratio}%"};
    max-width: #{"#{$ratio}%"};
  }
}
@mixin cols-col($x) {
  $ratio: 100 / $x;
  > :nth-child(1):nth-last-child(#{$x}) {
    &, & ~ * {
      flex: 0 0 #{"#{$ratio}%"};
      max-width: #{"#{$ratio}%"};
    }
  }
}
@mixin gx($x) {
  margin-left: -$x;
  margin-right: -$x;
  > * {
    padding-left: $x;
    padding-right: $x;
  }
}
@mixin gy($x) {
  margin-top: -$x;
  margin-bottom: -$x;
  > * {
    padding-top: $x;
    padding-bottom: $x;
  }
}
@mixin layouts {
  .ctn {
    width: 100%;
    margin-right: auto;
    margin-left: auto;
    @include media-sm {
      max-width: $media-sm-max-width;
    }
    @include media-md {
      max-width: $media-md-max-width;
    }
    @include media-lg {
      max-width: $media-lg-max-width;
    }
    @include media-xl {
      max-width: $media-xl-max-width;
    }
  }
  .row {
    display: flex;
    flex-wrap: wrap;
    > * {
      width: 100%;
    }
    @for $i from 1 through 12 {
      @include row-col('col', $i);
    }
    @include media-sm {
      @for $i from 1 through 12 {
        @include row-col('col-sm', $i);
      }
    }
    @include media-md {
      @for $i from 1 through 12 {
        @include row-col('col-md', $i);
      }
    }
    @include media-lg {
      @for $i from 1 through 12 {
        @include row-col('col-lg', $i);
      }
    }
    @include media-xl {
      @for $i from 1 through 12 {
        @include row-col('col-xl', $i);
      }
    }
  }
  .cols {
    display: flex;
    text-align: center;
    > * {
      --fg: inherit;
      width: 100%;
      height: 100%;
      &[data-active="true"] {
        --fg: var(--primary);
      }
    }
    @for $i from 1 through 10 {
      @include cols-col($i);
    }
  }
  .w-tabs > * {
    > div {
      > span {
        display: inline-block;
        width: .5em;
        height: 1em;
        margin-left: 4px;
        vertical-align: text-bottom;
        &:before, &:after {
          content: '';
          display: block;
          width: .5em;
          height: 40%;
          border-left: .25em solid transparent;
          border-right: .25em solid transparent;
          color: var(--sep);
        }
        &:before {
          margin: 5% auto 10%;
          border-bottom: .3em solid;
        }
        &:after {
          margin: 10% auto 5%;
          border-top: .3em solid;
        }
      }
    }
    > input {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      border: none;
      background: none;
      appearance: none;
      // visibility: hidden;
      // opacity: 0;
      &[type="radio"]:checked ~ div:after {
        content: '';
        display: block;
        width: 1.2em;
        height: 2px;
        position: absolute;
        top: 100%;
        left: 0;
        right: 0;
        margin: 2px auto 0;
        background-color: currentColor;
      }
      &[type="checkbox"]:not(:checked) ~ div > span:before {
        color: unset;
      }
      &[type="checkbox"]:checked ~ div > span:after {
        color: unset;
      }
    }
  }
  .gx { @include gx($sx / 2); }
  .gy { @include gy($sy / 2); }
  .gx-xs { @include gx($dims-xs / 2); }
  .gx-sm { @include gx($dims-sm / 2); }
  .gx-md { @include gx($dims-md / 2); }
  .gx-lg { @include gx($dims-lg / 2); }
  .gx-xl { @include gx($dims-xl / 2); }
  .gy-xs { @include gy($dims-xs / 2); }
  .gy-sm { @include gy($dims-sm / 2); }
  .gy-md { @include gy($dims-md / 2); }
  .gy-lg { @include gy($dims-lg / 2); }
  .gy-xl { @include gy($dims-xl / 2); }
  [class|="br"] { height: 1.2em; }
  .br-xs { height: $dims-xs; }
  .br-sm { height: $dims-sm; }
  .br-md { height: $dims-md; }
  .br-lg { height: $dims-lg; }
  .br-xl { height: $dims-xl; }
  .br-header { height: $header-h; }
  .br-subheader { height: $subheader-h; }
  .br-footer { height: $footer-h; }
  @for $i from 1 through 10 {
    $h: $i * 10;
    .br-#{$h} {
      height: #{$h}vh;
    }
  }
}

// Icons
@mixin icons {
  [class|="icon"] {
    display: inline-block;
    min-width: 1.2em;
    min-height: 1.2em;
    vertical-align: text-bottom;
    &:before, &:after {
      position: absolute;
      display: inline-block;
      transition: none $duration ease-out;
    }
  }
  .icon-burger {
    &:before, &:after {
      content: '';
      left: calc(50% - .6em);
      width: 1.2em;
      height: var(--stroke-width, 1px);
      background-color: currentColor;
      transition-property: transform, top;
    }
    &:before {
      top: calc(50% - .4em);
      box-shadow: 0 .4em 0 0 currentColor;
    }
    &:after {
      top: calc(50% + .4em);
    }
    &[data-active="true"] {
      &:before {
        top: 50%;
        transform: rotate(45deg);
        box-shadow: none;
      }
      &:after {
        top: 50%;
        transform: rotate(-45deg);
      }
    }
  }
  .icon-x {
    &:before, &:after {
      content: '';
      top: 50%;
      left: calc(50% - .6em);
      width: 1.2em;
      height: var(--stroke-width, 1px);
      background-color: currentColor;
    }
    &:before {
      transform: rotate(45deg);
    }
    &:after {
      transform: rotate(-45deg);
    }
  }
  .icon-ok:before {
    content: '';
    top: calc(50% + .1em);
    left: calc(50% - .6em);
    width: 1.2em;
    height: .5em;
    border-left: var(--stroke-width, 1px) solid;
    border-bottom: var(--stroke-width, 1px) solid;
    border-bottom-left-radius: 2px;
    transform-origin: left top;
    transform: rotate(-45deg);
  }
  [class|="icon-chevron"]:after {
    content: '';
    top: calc(50% - .5em);
    left: calc(50% - .2em);
    width: 0.8em;
    height: 0.8em;
    border-left: var(--stroke-width, 1px) solid;
    border-bottom: var(--stroke-width, 1px) solid;
    border-bottom-left-radius: 2px;
    transform-origin: 25% 75%;
    transition-property: transform;
  }
  .icon-chevron-b {
    &:after {
      transform-origin: 25% 70%;
      transform: rotate(-45deg);
    }
    &[data-active="true"]:after {
      transform: rotate(135deg);
    }
  }
  .icon-chevron-t {
    &:after {
      transform-origin: 25% 70%;
      transform: rotate(135deg);
    }
    &[data-active="true"]:after {
      transform: rotate(315deg);
    }
  }
  .icon-chevron-l {
    &:after {
      top: calc(50% - .6em);
      transform: rotate(45deg);
    }
    &[data-active="true"]:after {
      transform: rotate(225deg);
    }
  }
  .icon-chevron-r {
    &:after {
      top: calc(50% - .6em);
      transform: rotate(225deg);
    }
    &[data-active="true"]:after {
      transform: rotate(405deg);
    }
  }
}

// Components
@mixin components {
  .overlay {
    z-index: 3;
    display: none;
    position: fixed;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100vh;
    @include backdrop(var(--overlay));
    // @include transition-ease(opacity);
    &[data-active="true"] {
      display: block;
    }
    > * {
      word-break: break-all;
    }
    .dialog {
      position: absolute;
      top: 50%;
      left: 50%;
      margin-right: -50%;
      transform: translate(-50%, -50%);
      width: 80%;
      > main {
        padding: $dy $dx;
        max-height: 80vh;
        overflow-y: auto;
        overscroll-behavior-y: contain;
      }
    }
    .action-sheet, .modal {
      overflow-y: auto;
      overscroll-behavior-y: contain;
    }
    .action-sheet {
      top: 20vh;
      height: 80vh;
    }
    .modal {
      height: 100vh;
      padding-bottom: 1px;
    }
  }
  .toast {
    // TODO
  }
  .icon {
    width: auto;
    height: 1.2em;
    &, & + span {
      vertical-align: text-bottom;
    }
  }
  .avatar {
    border-radius: 50%;
    background-color: var(--body-bg);
    object-fit: cover;
  }
  img.block {
    width: 100%;
    height: auto;
  }
  img:not([src]) {
    opacity: 0;
  }
  .form {
    > label > :first-child, > * > label:first-child {
      --fg: var(--info);
      display: block;
      margin-top: 1em;
      margin-bottom: .5em;
      min-height: 1.2em;
      + * {
        display: block;
        width: 100%;
        padding: 1em;
        border: 1px solid var(--main-bg);
        background-color: var(--main-bg);
      }
    }
    > * {
      > :only-child {
        margin-top: 2.7em;
      }
      input, textarea, select, button {
        appearance: none;
        &[disabled] {
          opacity: 1;
          border-color: var(--na);
          background-color: var(--na);
        }
        &.border {
          border-color: var(--sep);
        }
      }
      [type="color"], [type="date"], [type="datetime-local"], [type="month"], [type="time"], [type="week"] {
        height: calc(3.2em + 2px);
      }
      [type="file"] {
        font-size: 90%;
      }
      [type="range"] {
        -webkit-appearance: slider-horizontal;
        -moz-appearance: range;
      }
      [type="checkbox"], [type="radio"] {
        width: 1.2em;
        height: 1.2em;
        vertical-align: text-bottom;
        border: 1px solid var(--body-bg);
        background-color: var(--body-bg);
        @include transition-ease;
      }
      [type="checkbox"] {
        border-radius: $radius;
        &:checked {
          border-color: var(--primary);
          background-color: var(--primary);
        }
      }
      [type="radio"] {
        border-width: .4em;
        border-radius: 50%;
        &:checked {
          border-color: var(--primary);
          background-color: var(--main-bg);
        }
      }
      select, [type="date"], [type="datetime-local"], [type="month"], [type="time"], [type="week"] {
        --fg: var(--hint);
        background: url('data:image/svg+xml;utf8,<svg xmlns="http://www.w3.org/2000/svg" width="48" height="48" viewBox="0 0 24 24" stroke="%23999" stroke-linecap="round" stroke-linejoin="round" fill="none" color="%23999"><path d="M8 8.333l4-4 4 4h0M16 15.667l-4 4-4-4h0"/></svg>') center right 1em/1.5em no-repeat var(--main-bg);
      }
    }
  }
  .input {
    display: block;
    width: 100%;
    min-height: calc(3.2em + 2px);
    padding: 1em $sx;
    margin-bottom: 1em;
    border-radius: 0;
    border: 1px solid var(--sep);
    background: none;
    appearance: none;
  }
  button, input, select, textarea {
    &.reset {
      min-height: 3.2em;
      border-radius: 0;
      border: 1px solid transparent;
      background: none;
      appearance: none;
      &.border {
        border-color: var(--sep);
      }
    }
  }
  button.reset {
    text-align: left;
  }
  [type="color"], [type="date"], [type="datetime-local"], [type="month"], [type="time"], [type="week"] {
    &.reset {
      height: calc(3.2em + 2px);
    }
  }
  [type="file"].reset {
    font-size: 90%;
  }
  [type="range"].reset {
    -webkit-appearance: slider-horizontal;
    -moz-appearance: range;
  }
  [type="checkbox"], [type="radio"] {
    &.reset {
      min-height: unset;
      width: 1.2em;
      height: 1.2em;
      vertical-align: text-bottom;
      border: 1px solid var(--hint);
      @include transition-ease;
    }
  }
  [type="checkbox"]:not([data-radio]).reset, [type="radio"][data-checkbox].reset {
    &:checked {
      border-color: var(--primary);
      background-color: var(--primary);
      background: url('data:image/svg+xml;utf8,<svg xmlns="http://www.w3.org/2000/svg" width="48" height="48" viewBox="0 0 24 24" stroke="white" stroke-width="2" stroke-linecap="round" stroke-linejoin="round" fill="none" color="white"><path d="M4 13l5 5L20 7"/></svg>') center/1.2em no-repeat var(--primary);
    }
  }
  [type="radio"]:not([data-checkbox]).reset, [type="checkbox"][data-radio].reset {
    border-width: .4em;
    border-radius: 50%;
    background-color: var(--body-bg);
    &:checked {
      border-color: var(--primary);
      background-color: var(--main-bg);
    }
  }
  select, [type="date"], [type="datetime-local"], [type="month"], [type="time"], [type="week"] {
    &.reset {
      --fg: var(--hint);
      background: url('data:image/svg+xml;utf8,<svg xmlns="http://www.w3.org/2000/svg" width="48" height="48" viewBox="0 0 24 24" stroke="%23999" stroke-linecap="round" stroke-linejoin="round" fill="none" color="%23999"><path d="M8 8.333l4-4 4 4h0M16 15.667l-4 4-4-4h0"/></svg>') center right 1em/1.2em no-repeat var(--main-bg);
    }
  }
  textarea.reset {
    padding: .75em;
  }
  input.switch {
    -webkit-appearance: none;
    -moz-appearance: none;
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    background: none;
    border: none;
    + div {
      border: 1px solid var(--hint);
      padding: .5em 1em;
    }
    &:checked + div {
      --fg: var(--invert);
      border-color: var(--major);
      background-color: var(--major);
    }
    + svg {
      fill: none;
    }
    &:checked + svg {
      fill: var(--primary);
    }
  }
  .li {
    display: flex;
    // padding-left: var(--edge-x, 0);
    // padding-right: var(--edge-x, 0);
    background-color: var(--bg, unset);
    &.w-link:after {
      flex-shrink: 0;
      align-self: center;
    }
    > * {
      // flex: 0 1 auto; // default
      word-break: break-all;
      + * {
        margin-left: $sx;
      }
    }
    > main, > dt {
      flex-grow: 1;
    }
    > main, > main + * {
      align-self: center;
    }
    > dt, > dd, > .label {
      padding-top: 1em;
      padding-bottom: 1em;
      min-height: 3.2em;
    }
    > dd, > .label + * {
      --fg: var(--hint);
    }
    > dt.shift {
      --fg: var(--hint);
      + dd {
        --fg: inherit;
      }
    }
    .label {
      width: 30%;
      min-width: 30%;
      max-width: 30%;
      padding-top: 1em;
      padding-bottom: 1em;
      + * {
        flex-grow: 1;
        align-self: center;
      }
      + input {
        min-height: 3.2em;
      }
    }
    > .heading {
      margin-top: .5em;
      margin-bottom: .5em;
      font-size: $size-lg;
      font-weight: bolder;
      + * {
        --fg: var(--hint);
        font-size: 14px;
      }
    }
  }
  a.li {
    --fg: inherit;
  }
  button.li {
    width: 100%;
    text-align: inherit;
  }
  .list {
    &:empty:before, &:empty:after {
      display: block;
      text-align: center;
    }
    &:empty:before {
      content: var(--empty-icon, '🕸');
      padding: 20vh $dx 0;
      font-size: 4em;
    }
    &:empty:after {
      content: var(--empty-text, '没有内容');
      margin-top: 1em;
      padding: 0 $dx 20vh;
      font-size: $size-sm;
      color: var(--hint);
    }
    &.row:empty, &.cols:empty {
      flex-direction: column;
    }
  }
  .w-sep {
    min-height: 1px;
  }
  .w-sepv {
    min-width: 1px;
  }
  .w-sep:before, .w-sepv:before, .w-sep-b:after, .w-seps > :nth-child(n + 2):before, .w-seps-tb:not(:empty):before, .w-seps-tb:not(:empty):after {
    content: '';
    z-index: 1;
    display: block;
    position: absolute;
    background-color: var(--sep);
  }
  .w-sepv:before {
    left: 0;
    width: 1px;
    height: 100%;
  }
  .w-sep:before, .w-sep-b:after, .w-seps > :nth-child(n + 2):before, .w-seps-tb:not(:empty):before, .w-seps-tb:not(:empty):after {
    top: 0;
    // left: var(--edge-x, 0);
    // right: var(--edge-x, 0);
    left: 0;
    right: 0;
    height: 1px;
  }
  .w-sep-b:after, .w-seps-tb:not(:empty):after {
    top: unset;
    bottom: 0;
  }
  .w-link:after {
    content: '';
    display: inline-block;
    width: 0.5em;
    height: 0.5em;
    margin-left: .25em;
    margin-right: .17em;
    vertical-align: .1em;
    border-left: 1px solid var(--c-w-link, var(--fg));
    border-bottom: 1px solid var(--c-w-link, var(--fg));;
    // border-left: 1px solid var(--c-w-link, var(--primary));
    // border-bottom: 1px solid var(--c-w-link, var(--primary));;
    transform-origin: 50%;
    transform: rotate(225deg);
  }
  .badge {
    --set-radius: .75em;
    display: inline-block;
    padding: .15em .25em;
    min-width: 1.5em;
    line-height: 1;
    white-space: nowrap;
    vertical-align: text-bottom;
    text-align: center;
    background-color: var(--bg, var(--main-bg));
    &:empty {
      padding: 0;
      min-width: .5em;
      height: .5em;
      vertical-align: text-top;
      border-radius: 50%;
    }
    &.border {
      border-color: var(--invert);
    }
  }
  .tr-badge {
    display: inline-block;
    > .badge {
      position: absolute;
      top: -.55em;
      right: -.75em;
    }
  }
  .tag {
    display: inline-block;
    padding: .2em .5em;
    background-color: var(--bg, var(--main-bg));
    word-break: break-all;
  }
  .btn {
    display: inline-block;
    text-align: center;
    vertical-align: middle;
    cursor: pointer;
    padding: .5em 1em;
    border: 1px solid;
    border-radius: var(--set-radius, 0);
    border-color: var(--bg, transparent);
    background-color: var(--bg, initial);
    @include transition-ease;
    &[disabled] {
      border-color: var(--na);
      background-color: var(--na);
      cursor: not-allowed;
    }
    &.block {
      padding: 1em;
    }
  }
  button {
    border: none;
    background-color: transparent;
  }
  button, input, textarea {
    &.block {
      width: 100%;
    }
  }
  .nav-ctn {
    display: flex;
    align-items: flex-start;
    flex-wrap: wrap;
    > * {
      flex: 1 0;
    }
    > nav {
      width: 100%;
      flex: 0 0;
      > main {
        display: none;
      }
      &[data-active="true"] > main {
        display: block;
      }
    }
    > main {
      max-width: 100%;
      padding: $dy $dx;
    }
    @include media-desktop {
      flex-wrap: nowrap;
      > nav {
        width: auto;
        margin: $dy * 2 $dx 0 0;
        > button {
          display: none;
        }
        > main {
          display: block;
        }
      }
      > main {
        padding: $dy * 2 $dx;
      }
    }
  }
  .ar1-1 { width: 100%; padding-top: 100%; > * { margin-top: -100%; } }
  .ar2-1 { width: 100%; padding-top: 50%; > * { margin-top: -50%; } }
  .ar4-3 { width: 100%; padding-top: 75%; > * { margin-top: -75%; } }
}

