// App
.page {
  $header-z-index: 2;
  @include icons;
  @include layouts;
  @include components;
  width: 100%;
  min-height: 100vh;
  // Mobile
  > header, > footer, > nav {
    display: block;
    z-index: $header-z-index;
    left: 0;
    right: 0;
    width: 100%;
  }
  > header {
    min-height: $header-h;
    padding-top: $header-h;
    @include sticky(0, $header-z-index);
    &:before {
      content: '';
      display: block;
      width: 100%;
      height: 100%;
      position: absolute;
      top: 0;
      left: 0;
      @include backdrop(var(--header-bg));
    }
    > .l, > .r {
      z-index: 1;
      position: absolute;
      top: 0;
      padding: 0 $dx;
      height: $header-h;
      line-height: $header-h;
    }
    > .l {
      left: 0;
    }
    > .r {
      right: 0;
    }
    > .c {
      position: absolute;
      top: $header-h / 2;
      left: 15%;
      right: 15%;
      text-align: center;
      font-weight: normal;
      font-size: $size-lg;
      transform: translateY(-50%);
      white-space: nowrap;
      overflow: hidden;
      text-overflow: ellipsis;
    }
    > .subheader {
      height: $subheader-h;
    }
    a {
      --fg: inherit;
    }
    &.scroll-to-show {
      position: fixed;
      &:before, > .c, > .subheader {
        opacity: calc(var(--window-scroll-y, 0) / #{($header-h + $subheader-h) / 1px});
      }
      ~ main {
        min-height: calc(100vh + #{$header-h + $subheader-h});
      }
    }
  }
  > footer {
    height: $footer-h;
    position: fixed;
    left: 0;
    bottom: 0;
    .btn {
      height: $footer-h;
      line-height: $footer-h;
      padding: 0;
    }
    &:empty {
      display: none;
    }
    .subfooter {
      position: absolute;
      left: 0;
      bottom: $footer-h;
      width: 100%;
    }
  }
  > main {
    max-width: 100%;
    padding: $dy $dx;
    > .expd {
      // --edge-x: #{$dx};
      margin-left: -$dx;
      margin-right: -$dx;
      &:first-child {
        margin-top: -$dy;
      }
      &:last-child {
        margin-bottom: -$dy;
      }
    }
  }
  @include media-mobile {
    * {
      outline: none;
    }
    :not(input, textarea, p, image) {
      -webkit-touch-callout: none;
      user-select: none;
    }
  }
  .user-select {
    -webkit-touch-callout: default;
    user-select: text;
  }
  // Desktop
  > nav {
    height: $nav-h;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    @include sticky(0, $header-z-index);
    @include backdrop(var(--nav-bg));
    &[data-active="true"] > main {
      height: calc(100vh - #{$nav-h});
      visibility: visible;
      opacity: 1;
    }
    > * {
      height: 100%;
    }
    > button {
      --fg: var(--hint);
      width: $nav-h;
      &:hover {
        background-color: var(--main-bg);
      }
    }
    > header {
      flex-grow: 1;
    }
    > main {
      z-index: 0;
      width: 100%;
      overscroll-behavior: contain;
      overflow-y: auto;
      visibility: hidden;
      opacity: 0;
      @include transition-ease(opacity);
      &:after {
        content: '';
        display: block;
        height: 1px;
      }
      > .menu {
        min-height: 100%;
        background-color: var(--nav-menu-bg);
      }
      .menu > * {
        --fg: inherit;
        display: block;
        white-space: nowrap;
        &:hover, &[data-active="true"] {
          &, & > :first-child {
            color: var(--primary);
          }
        }
        > .menu {
          margin-left: 1em;
        }
      }
      .menu > a, .menu > :not(a) > :first-child {
        padding: 1em;
      }
    }
    @include media-desktop {
      $nav-h: 80px;
      height: $nav-h;
      flex-wrap: nowrap;
      &[data-active="true"] > main {
        height: $nav-h;
      }
      > button {
        display: none;
      }
      > footer {
        order: 1;
      }
      > main {
        width: auto;
        overflow-y: initial;
        visibility: visible;
        opacity: 1;
        &:after {
          content: initial;
          display: none;
        }
        > .menu {
          display: flex;
          min-height: initial;
          background-color: initial;
          top: 50%;
          transform: translateY(-50%);
        }
        .menu > * {
          &:not(a) > :first-child {
            &:after {
              content: '';
              display: inline-block;
              width: .6em;
              height: .6em;
              margin-left: .5em;
              border-left: 1px solid;
              border-bottom: 1px solid;
              border-bottom-left-radius: 2px;
              transform-origin: left;
              transform: rotate(-45deg);
              @include transition-ease(transform);
            }
          }
          &:hover > .menu {
            visibility: visible;
            opacity: 1;
          }
          > .menu {
            z-index: 1;
            position: absolute;
            left: 50%;
            top: 100%;
            margin-left: 0;
            background-color: var(--main-bg);
            visibility: hidden;
            opacity: 0;
            @include shadow;
            @include transition-ease;
          }
        }
      }
    }
    + header {
      top: $nav-h;
    }
  }
  &:not(:last-of-type) {
    display: none;
  }
  @include helpers;
}
