@import "normalize.css";
@import "./swiper.css";
@import "./reset.scss";

:root {
  --swiper-theme-color: var(--primary);
}

.page .list:empty:before {
  color: transparent;
  background: url('/images/common/empty.svg') center bottom/auto 1em no-repeat;
}

.table-ctn {
  max-width: 100vw;
  overflow-y: auto;
  &.yltd {
    height: calc(80vh - var(--nav-h));
  }
  > table {
    width: 100%;
    border-collapse: collapse;
    background-color: var(--main-bg);
    tr:nth-child(n + 3) {
      border-top: 1px solid var(--sep);
    }
    th, td {
      height: 3em;
      padding: 1em .5em;
      &:first-child {
        padding-left: 1em;
      }
      &:last-child {
        padding-right: 1em;
      }
      a, button {
        white-space: nowrap;
      }
    }
    th {
      z-index: 1;
      position: sticky;
      text-align: left;
      vertical-align: bottom;
      top: 0;
      color: var(--hint);
      background-color: #FAFAFA;
      white-space: nowrap;
    }
  }
}
